@-ms-viewport {
  width: device-width;
}
html,
body {
  height: 100%;
  cursor: default;
}
html,
body,
p,
h1,
h2,
h3,
pre,
ul,
li {
  padding: 0;
  margin: 0;
}
body {
  background: #f9f9f9;
  color: #767676;
  font-family: sans-serif;
  font-size: 16px;
  font-family: 'Open Sans', sans-serif;
  font-weight: 300;
}
.content {
  max-width: 640px;
  margin: 0 10%;
  text-align: left;
}
p,
h1,
h2,
h3,
pre,
ul {
  line-height: 1.5;
}
p,
h2,
h3 {
  margin-top: 10px;
  margin-bottom: 20px;
  line-height: 1.75;
}
ul {
  padding-left: 2em;
  margin-top: -1em;
  margin-bottom: 1em;
}
a {
  color: #2c9e54;
  text-decoration: none;
  border-bottom: solid 1px rgba(44,158,84,0.5);
  transition: all ease 0.123s;
}
a:hover,
a:active {
  border-bottom-color: rgba(44,158,84,0.2);
}
h1 {
  font-size: 2.5em;
  font-weight: 100;
  line-height: 1.1;
}
h2 {
  font-size: 1.8em;
  color: #2c9e54;
  margin-top: 4em;
}
h3 {
  font-size: 1.4em;
  color: #000;
  margin: 1.5em 0 0;
}
.header {
  color: #2c9e54;
  max-width: 640px;
  margin: 0 5%;
  padding: 80px 0 60px;
}
.navigation a {
  margin-right: 15px;
}
.part {
  margin-bottom: 40px;
}
.footer {
  max-width: 640px;
  margin: 0 10%;
  padding-bottom: 60px;
}
.footer p {
  line-height: 1.2;
  font-size: 14px;
}
@media only screen and (min-width: 640px) {
  body {
    font-size: 20px;
  }
  .part {
    margin-bottom: 80px;
  }
}
